/* eslint-disable amp/no-console */

import Fuse from 'fuse.js'
import * as readline from 'node:readline'

interface Message {
	id: number
	type: string
	files?: string[]
	text?: string
}

interface Response {
	id: number
	type: 'response' | 'error'
	status?: 'ok'
	results?: string[]
	message?: string
}

class FuzzyServer {
	private fuse: Fuse<{ value: string; original: string }>

	constructor() {
		this.fuse = new Fuse([], {
			keys: ['value'],
			includeScore: true,
			location: 0,
			threshold: 0.7,
			distance: 256,
			ignoreLocation: true,
			findAllMatches: true,
			isCaseSensitive: false,
		})
	}

	addFiles(filePaths: string[]): void {
		for (const file of filePaths) {
			this.fuse.add({ value: file.toLowerCase(), original: file })
		}
	}

	removeFiles(filePaths: string[]): void {
		for (const file of filePaths) {
			this.fuse.remove((f) => f.value === file)
		}
	}

	query(text: string, limit = 50): string[] {
		const results = this.fuse.search(text, { limit })
		return results.map((result) => result.item.original)
	}

	handleMessage(message: Message): Response {
		try {
			const { id, type } = message

			switch (type) {
				case 'index:add':
					this.addFiles(message.files || [])
					return { id, type: 'response', status: 'ok' }

				case 'index:remove':
					this.removeFiles(message.files || [])
					return { id, type: 'response', status: 'ok' }

				case 'query': {
					const results = this.query(message.text || '')
					return { id, type: 'response', results }
				}

				default:
					return { id, type: 'error', message: `Unknown type: ${type}` }
			}
		} catch (error) {
			return {
				id: message.id,
				type: 'error',
				message: error instanceof Error ? error.message : String(error),
			}
		}
	}

	start(): void {
		const rl = readline.createInterface({
			input: process.stdin,
			output: process.stdout,
			terminal: false,
		})

		rl.on('line', (line) => {
			try {
				const message = JSON.parse(line) as Message
				const response = this.handleMessage(message)
				console.log(JSON.stringify(response))
			} catch (error) {
				console.log(
					JSON.stringify({
						id: -1,
						type: 'error',
						message: `Parse error: ${error instanceof Error ? error.message : String(error)}`,
					}),
				)
			}
		})

		rl.on('close', () => {
			process.exit(0)
		})
	}
}

const server = new FuzzyServer()
server.start()
